@font-face {
    font-family: "Montserrat";
    font-weight: 400;
    font-style: normal;
    src: url("./assets/fonts/Montserrat-Regular.woff2") format("woff2"),
            url("./assets/fonts/Montserrat-Regular.woff") format("woff");
}

@font-face {
    font-family: "Montserrat";
    font-weight: 500;
    font-style: normal;
    src: url("./assets/fonts/Montserrat-Medium.woff2") format("woff2"),
            url("./assets/fonts/Montserrat-Medium.woff") format("woff");
}

@font-face {
    font-family: "Montserrat";
    font-weight: 600;
    font-style: normal;
    src: url("./assets/fonts/Montserrat-SemiBold.woff2") format("woff2"),
            url("./assets/fonts/Montserrat-SemiBold.woff") format("woff");
}

@font-face {
    font-family: "Montserrat";
    font-weight: 700;
    font-style: normal;
    src: url("./assets/fonts/Montserrat-Bold.woff2") format("woff2"),
            url("./assets/fonts/Montserrat-Bold.woff") format("woff");
}

@font-face {
    font-family: "Montserrat";
    font-weight: 900;
    font-style: normal;
    src: url("./assets/fonts/Montserrat-Bold.woff2") format("woff2"),
            url("./assets/fonts/Montserrat-Bold.woff") format("woff");
}

/* variables */
$slope: 0.5; /* pente */
$angle: 26.57deg;
$space-character-top: 70px;
$space-elements: 7px;
$gutter-vert: 15px;
$gutter-horiz: 4vw;
$gutter-banner-vert: 15px;
$gutter-banner-horiz: 3vw;

$color-blue-sky: #b5ffff;
$color-green-grass: #5abf7d;
$color-brown-light: #f2ca80;
$color-brown-dark: #44200e;
$color-text: #44200E;

$transition-duration: 0.5s;

/* normalize */
* {
    padding: 0;
    margin: 0;
}
ul, ol {
    list-style: none;
}
button {
    border: none;
    background: none;
    cursor: pointer;
}

/* general properties */

body {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    color: $color-text;
}

body, #root, .App, .page {
    overflow: hidden;
}

.page {
    position: absolute;
    top: 0;
    width: 100%;
    main.wrapper {
        position: relative;
        width: var(--wrapper-width);
        max-width: var(--max-width);
        height: 100vh;
        margin: auto;
    }
}

/* z-indexes */

.page {
    z-index: 1;
    main.wrapper {
        z-index: 2;
    }
    &.home {
        .fg {
            z-index: 4;
        }
        .character-wrapper {
            z-index: 3;
        }
    }
    .decor {
        .tree { z-index: 2; }
        .grass { z-index: 1; }
    }
    .tooltip, .navbar {
        z-index: 10;
    }
    .cart .content {
        z-index: 6;
        .banner { z-index: 5; }
    }
    .banner {
        z-index: 5;
    }
    .composed-img {
        .bg     { z-index: 2; }
        .icon   { z-index: 3; }
        .fg     { z-index: 4; }
    }
}

.composed-img {
    display: grid;
    grid-template: "center" 1fr / 1fr;
    > * {
        width: 100%;
        grid-area: center;
    }
}

.button {
    border-radius: 8px;
    font-weight: 700;
    padding: 4px 1em;
    font-size: 1rem;
    font-family: 'Montserrat', sans-serif;
    box-shadow: 2px 1px 6px rgba(0, 0, 0, 0.3);
    &.primary {
        background: $color-brown-dark;
        color: $color-brown-light;
    }
    &.secondary {
        background: $color-brown-light;
        color: $color-brown-dark;
    }
    &.toggle {
        font-weight: 500;
        box-shadow: none;
        background: none;
        border: 1.5px solid $color-brown-dark;
        color: $color-brown-dark;
        padding-right: 8px;
        padding-left: 8px;
        &.selected {
            background: $color-brown-dark;
            color: $color-brown-light;
        }
    }
    &:disabled {
        box-shadow: none;
        opacity: 0.5;
    }
}

.hidden {
    display: none;
}

.character-wrapper {
    &.walking.r {
        .character {
            animation-name: walking-right;
        }
    }
    &.walking.l {
        .character {
            animation-name: walking-left;
        }
    }
    &.walking {
        .character {
            animation-iteration-count: infinite;
            animation-direction: alternate;
            animation-timing-function: linear;
            animation-duration: 0.5s;
        }
    }
}

@keyframes walking-right {
    0% {
        transform: rotate(0) scaleY(1) translateY(0);
    }
    100% {
        transform: rotate(10deg) scaleY(0.7) translateY(15%);
    }
}
@keyframes walking-left {
    0% {
        transform: rotate(0) scaleY(1) translateY(0);
    }
    100% {
        transform: rotate(-10deg) scaleY(0.7) translateY(15%);
    }
}

%on-the-road {
    background: $color-green-grass;
    main.wrapper {
        .btn-toggle {
            width: 100%;
        }
        .character-wrapper {
            position: absolute;
            width: 40%;
            top: calc(var(--window-height) / 2 + #{$space-character-top});
            left: 5%;
            z-index: 5;
        }
    }
}